import React from "react"
import { Link } from 'react-router-dom'
import {Navbar, Nav} from 'react-bootstrap'
import logo from '../assets/Logo.jpg'
function Header(props){
  return(
    <div>
        <p style={{fontSize:".7rem", marginBottom: "0", textAlign: "center",  backgroundColor: '#fcfcfc'}}>
        CALL NOW: <a style={{color: "inherit"}}href="tel:678-580-1149">(678)-580-1149</a>
        </p>
      <Navbar collapseOnSelect sticky = "top"  expand= "lg">
        <Navbar.Brand>
          <Link to= "/">
            <img
              src = {logo}
              className = 'd-inline-block align-top'
              alt='Home'
            />
        </Link>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav" className = "justify-content-end">
        <Nav className="justify-content-end">
          <Link to = '/'          className = 'Header-Link'>Home </Link>
          <Link to = '/meet'      className = 'Header-Link'>The Doctor </Link>
          <Link to = '/services'  className = 'Header-Link'>Services </Link>
          <Link to = '/contact'   className = 'Header-Link'>Contact Us  </Link>
          <Link to = '/review'    className = 'Header-Link'>Review </Link>
        </Nav>
      </Navbar.Collapse>
      </Navbar>
      </div>
  )}


export default Header
