import React from "react"
import Header from '../components/Header'
import Footer from '../components/Footer'
import {Container, Jumbotron } from 'react-bootstrap'
import Map from "../components/Map"
import Banner from "../components/Banner"
import banner from "../assets/1.jpg"
function Contact(){
  return(
    <Container fluid = {true} className="container">
      <Header />
        <Banner
          background = {banner}
          text = 'Get in Touch'
          />
        <Jumbotron>
            <h4 className = "center-text">
              If you feel it is the appropriate time to see the doctor, get in contact
              and let's schedule and appointment.<br />Give us a call at <a href="tel:678-580-1149">678-580-1149</a> or
              send an email to <b>milnervain@gmail.com</b> and
              we can schedule an appointment.
            </h4>
            <p>

            </p>
          </Jumbotron>
            <br />
            <p></p>
          <Map />
        <Footer />
    </Container>
    )}

export default Contact
