import React from 'react'
import Header from '../components/Header'
import Footer from '../components/Footer'
import {Container,Jumbotron, Button } from 'react-bootstrap'
import Banner from "../components/Banner"
import banner from "../assets/2.jpg"
export default function ReviewPage() {
  return (
    <Container fluid = {true} className="container">
    <Header />
    <Banner
      text = "Tell us about your visit."
      background = {banner}
      />
    <Jumbotron className = "Lead-text">
      <h4>
        If you enjoyed your service, or if there is something that we could
        improve on, let us know by leaving an honest google review.</h4><br />
      <a href = "https://search.google.com/local/writereview?placeid=ChIJCUvnHXEH9YgRjuf00brVOcU">
        <Button>Let's Go</Button>
      </a><br /><br />
    <h4>See other visitors' reviews below</h4>
    </Jumbotron>
    <p>this is where the reviews will go. At present the button just takes you
       to the address of the office, since it is not registered as a buisness
     and reviews cannot be left</p>
    <Footer />
    </Container>
  )
}
