import React from "react"
import {Carousel} from 'react-bootstrap'
export default function Banner(props){
  return(
    <Carousel
    controls = {false}
    indicators = {false}
    >
    <Carousel.Item >
      <img
        className="d-block mw-100"
        src={props.background}
        alt="Doctor Page Banner"
      />
      <Carousel.Caption>
        <p className = "large-text">{props.text}</p>
      </Carousel.Caption>
    </Carousel.Item>
  </Carousel>
  )}
