import React from "react"
import Header from '../components/Header'
import Footer from '../components/Footer'
import {Container,Image, Row, Col} from 'react-bootstrap'
import DocArms from "../assets/doc2.jpg"
import docImage from "../assets/smallerdoc.jpg"
import Banner from "../components/Banner"
export default function Doctor(){
  return(
    <Container fluid = {true} className="container">
      <Header />
      <Banner
        background = {DocArms}
        text = 'About Dr. Timothy Milner, MD, FACS'
        />
      <Row>
        <Col xs={{span: 7, offset: 2 }} md={{span: 4,offset: 2}}>
          <Image src = {docImage} fluid/>
        </Col>
      </Row>
      <br />
      <Row>
        <Col xs={{offset: 2 }} style={{"paddingLeft":"0"}}>
          <h3><b>Dr.Timothy Milner</b></h3><br />
          <p style = {{"fontSize":"1.5em"}}>
            <b>Hometown:</b> INSERT HOMETOWN<br /><br />
            <b>Undergraduate Education:</b>Morehouse College<br /><br />
            <b>Graduate Education:</b> University of Tennessee, Memphis, College
               of Medicine and was completed at Norfolk, Virginia<br /><br />
            <b>Medical Education:</b> INSERT EDUCATION<br /><br />
            <b>Surgical Training:</b> INSERT EDUCATION<br /><br />
            <b>Vascular Surgery Training:</b>INSERT EDUCATION<br /><br />
            <b>Board Certified:</b>Yes<br /><br />
            <b>Memberships:</b>INSERT EDUCATION<br /><br />
            <b>In Practice Since:</b> 2008(?)<br /><br />
            <b>Accepting New Patients:</b> Yes<br /><br />
            <b>Special Interests:</b> INSERT EDUCATION<br /><br />
          </p>
        </Col>
      </Row>
      <Footer />
    </Container>
  )}
